'use client';
import { useEffect, useState } from 'react';
import { client } from '../client';
import {
	Card,
	CardDescription,
	CardHeader,
	CardTitle,
} from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import Image from 'next/image';
import { Playlist, TrackItem } from '@spotify/web-api-ts-sdk';

export type IPlaylist = Playlist<TrackItem>;

export default function MyPlaylists() {
	const [playlists, setPlaylists] = useState<IPlaylist[]>([]);
	const [loading, setLoading] = useState(true);

	useEffect(() => {
		const getPlaylists = async () => {
			try {
				const user = await client.currentUser.profile();
				const response = await client.playlists.getUsersPlaylists(user.id);
				setPlaylists(response.items);
			} catch (error) {
				console.error('Failed to fetch playlists:', error);
			} finally {
				setLoading(false);
			}
		};
		getPlaylists();
	}, []);

	const content = (
		<div className="container mx-auto py-8">
			<h1 className="text-4xl font-bold mb-8 text-white text-center">
				My Playlists
			</h1>
			<div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
				{loading
					? [...Array(6)].map((_, i) => (
							<Card
								key={i}
								className="overflow-hidden bg-black/60 border-neutral-800"
							>
								<Skeleton className="h-[200px] bg-neutral-800" />
								<CardHeader>
									<Skeleton className="h-6 w-2/3 bg-neutral-800" />
									<Skeleton className="h-4 w-full mt-2 bg-neutral-800" />
								</CardHeader>
							</Card>
						))
					: playlists.map(playlist => (
							<Card
								key={playlist.id}
								className="overflow-hidden hover:shadow-lg transition-shadow bg-black/60 border-neutral-800 hover:bg-black/80"
							>
								<div className="aspect-square relative">
									<Image
										src={
											playlist.images?.[0]?.url || '/playlist-placeholder.png'
										}
										alt={playlist.name}
										className="object-cover w-full h-full"
										width={100}
										height={100}
									/>
								</div>
								<CardHeader>
									<CardTitle className="line-clamp-1 text-white">
										{playlist.name}
									</CardTitle>
									<CardDescription className="line-clamp-2 text-neutral-400">
										{playlist.description || `${playlist.tracks.total} tracks`}
									</CardDescription>
								</CardHeader>
							</Card>
						))}
			</div>
		</div>
	);

	return (
		<main className="min-h-screen bg-neutral-950 bg-gradient-to-b from-emerald-950/50 to-neutral-950">
			{content}
		</main>
	);
}
